.App{
  min-height: 100vh;
  background-color: #f77604;
}

.search-form{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;


}.search-bar{
  width: 50%;
  border:none;
  padding: 10px;
}

.search-button{
  background: #964B00;
  border:none;
  padding: 10px 20px;
  cursor: pointer;
  color: white;
  text-transform: uppercase;
}

.search-button:hover{

  border-radius:2rem;
  transform: scale(1.2);

}

.recipes{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

